//import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";



import Header from './components/Header/Header.js';
import Footer from './components/Footer/Footer.js';
import Sidebar from './components/Sidebar/Sidebar.js';
import PageContentLanding from './components/PageContentLanding/PageContentLanding.js';
import AdminRouteMap from "./components/AdminRouteMap/AdminRouteMap";
import AdminDriverMap from "./components/AdminDriverMap/AdminDriverMap";
import AdminTable from "./components/AdminTable/AdminTable";

function App() {
  return (
  <Router>
    <div className="App">    
      <Switch>
        <Route exact path="/">
          <Header />
          <div className="container">
            <PageContentLanding />
            <Sidebar />
          </div>
          <Footer />
        </Route>
        <Route path="/about">
        <Header />  
        <div className="container">
          <div className="main-column">
            <p>Lorem ispum dolor.</p>
          </div>
          <Sidebar />
        </div>
        <Footer />
        </Route>

        <Route exact path="/admin/route-map" component={AdminRouteMap} />
        <Route exact path="/admin/driver-map" component={AdminDriverMap} />
        <Route exact path="/admin/depots" component={AdminTable} />
        <Route exact path="/admin/trucks" component={AdminTable} />
        <Route exact path="/admin/appliances" component={AdminTable} />
      </Switch>
    </div>
    </Router>  
  );
}

export default App;