import mapImage from './drivermap.jpg';
import './AdminDriverMap.css';
import Footer from "../Footer/Footer";
import AdminHeader from "../AdminHeader/AdminHeader";

function AdminDriverMap() {
    return (
        <div className="main-column">
          <AdminHeader />
          <h1>Driver Map</h1>
          <div className="img-wrap">
            <img src={mapImage} alt="Driver's Map" />
          </div>
          <Footer />
        </div>
    );
}

export default AdminDriverMap;